<template>
  <div class="provider">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <div class="inline-block ml-10" v-if="tableVal.length !== 0">
        <el-button type="danger" size="small" @click="stopBtn()">禁用</el-button>
        <el-button type="primary" size="small" @click="startBtn()">启用</el-button>
        <el-button type="primary" size="small" @click="weightBtn()">权重调节</el-button>
        <el-button type="primary" size="small" @click="loadChange()">负载均衡</el-button>
        <el-button type="primary" size="small" @click="demotionBtn()">降级</el-button>
        <el-button type="warning" size="small" @click="demotionRecover()">降级恢复</el-button>
      </div>
    </div>
    <el-form :model="providerForm" :rules="inputRules" ref="providerForm" label-width="92px" label-position="right" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('redis.redisCluster.clusterName')+'：'" prop="zkClusterId">
            <el-select v-model="providerForm.zkClusterId" clearable :placeholder="$t('common.pleaseChoose')" style="width:100%">
              <el-option
                v-for="(item) in clusterOption"
                :key="item.id"
                :label="item.pkDesc"
                :value="item.pkValue">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('service.serviceList.serviceName')+'：'" label-width="68px">
            <el-input type="text" v-model.trim="providerForm.serName" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('service.serviceList.applyName')+'：'" label-width="68px">
            <el-input type="text" v-model.trim="providerForm.appName" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('service.provider.ip')+'：'" label-width="40px">
            <el-input type="text" v-model.trim="providerForm.ip" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="1" :lg="24" align="right">
          <el-form-item label-width="0px">
            <el-button size="small" type="primary" icon="el-icon-search" @click="search('search')">{{$t('common.search')}}</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <!-- 表格部分 -->
    <el-table
      stripe
      :data="providerList[currentPage - 1]"
      v-loading="loading"
      ref="multipleTable"
      @selection-change="chooseBox"
      style="width: 100%">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <!-- 服务名 -->
      <el-table-column
        align="center"
        prop="serName"
        min-width="400"
        :label="$t('service.serviceList.serviceName')">
      </el-table-column>
      <el-table-column
        align="center"
        prop="appName"
        :label="$t('service.serviceList.applyName')"
        width="120">
        <template slot-scope="scope">
          {{ scope.row.appName | filterEmpty}}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="ip"
        :label="$t('service.provider.ip')"
        width="130">
      </el-table-column>
      <el-table-column
        align="center"
        prop="port"
        :label="$t('service.provider.port')"
        width="70">
      </el-table-column>
      <el-table-column
        align="center"
        prop="weight"
        :label="$t('service.provider.weight')"
        width="70">
      </el-table-column>
      <el-table-column
        align="center"
        :label="$t('service.provider.status')"
        width="90">
        <template slot-scope="scope">
          <el-tag :type="scope.row.serStatus ==='true' ? 'danger' : 'success'">{{ scope.row.serStatus ==='true' ? '已禁用' : '已启用' }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        :label="$t('service.provider.loadbBalance')"
        width="90">
        <template slot-scope="scope">
          {{ scope.row.loadbalance | loadBalance }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        :label="$t('service.provider.demotion')"
        width="90">
        <template slot-scope="scope">
          {{ scope.row.mockStatus | mockStatus }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="version"
        :label="$t('redis.redisList.version')"
        width="80">
      </el-table-column>
      <el-table-column
        align="center"
        prop="serGrop"
        show-overflow-tooltip
        :label="$t('service.provider.grouping')"
        width="90">
      </el-table-column>
      <el-table-column
        width="120"
        align="center"
        :label="$t('common.operation')"
        fixed="right">
        <template slot-scope="props">
          <el-dropdown trigger="click">
            <el-button type="text" size="mini">
              操作<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button type="text" size="mini" icon="fa fa-pencil-square-o" @click="weightBtn(props)">权重调节</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.serStatus === 'true'" type="text" size="mini" icon="fa fa-play"
                  @click="startBtn(props)" style="color:	#67c23a">启动
                </el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.serStatus === 'false'" type="text" size="mini" icon="fa fa-stop"
                  @click="stopBtn(props)"
                  style="color:	#FF0000">禁用
                </el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.mockStatus === '0'" type="text" size="mini" icon="fa fa-long-arrow-down"
                  @click="demotionBtn(props)" style="color:	#8B7D7B">降级
                </el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button v-if="props.row.mockStatus !== '0'" type="text" size="mini" icon="fa fa-long-arrow-up"
                  @click="demotionRecover(props)" style="color:	#67c23a">降级恢复
                </el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button type="text" size="mini" class="btn-text-blue" icon="fa fa-pencil-square-o"
                  @click="loadChange(props)">负载均衡
                </el-button>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next, jumper"
        background
        :total="providerTotal">
      </el-pagination>
    </div>
    <!-- 弹出框 -->
    <el-dialog :title="popWay" @close="serviceCancel()" :visible.sync="popBox" width="23%" center>
      <el-form ref="popForm" :model="popForm" label-width="100px" size="small" status-icon :rules="inputRules">
        <!-- 权重的配置 -->
        <el-form-item :label="$t('service.provider.weight')+'：'" v-if="popWay === $t('service.provider.weight')"
                      prop="ipPath">
          <el-input v-model.trim="popForm.weight" auto-complete="off"></el-input>
        </el-form-item>
        <!-- 降级的配置 -->
        <el-form-item :label="$t('service.provider.demotion')+'：'" v-if="popWay=== $t('service.provider.demotion')"
                      prop="port">
          <el-select v-model="popForm.demotion" clearable :placeholder="$t('common.pleaseChoose')">
            <el-option
              v-for="(item) in classify.demotionGroup"
              :key="item.id"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <!-- 负载的配置 -->
        <el-form-item :label="$t('service.provider.loadbBalance')+'：'"
                      v-if="popWay === $t('service.provider.loadbBalance')" prop="port">
          <el-select v-model="popForm.loadbBalance" clearable :placeholder="$t('common.pleaseChoose')">
            <el-option
              v-for="(item) in classify.loadGroup"
              :key="item.id"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="serviceOk()" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="serviceCancel()" size="small">{{$t('common.cancel')}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        loading: false,
        currentPage: 1, // 当前页数
        totalPage: 1, // 总页数
        pageSize: 10, // 每页条数
        // 查询规则
        inputRules: {
          zkClusterId: [
            {required: true, message: '请选择集群名称', trigger: 'blur'}
          ]
        },
        // 查询模板
        providerForm: {
          zkClusterId: '',
          appName: '',
          ip: '',
          serName: ''
        },
        // 获得表格多选按钮
        tableVal: [],
        serviceActive: '', // single:单个  more：多个
        // 弹出框
        popBox: false,
        // 弹出框titile
        popWay: '',
        popForm: {
          weight: '', // 权重
          demotion: '', // 降级
          loadbBalance: '' // 负载均衡
        }
      }
    },
    computed: {
      ...mapState({
        provideData: state => state.provider.provideData,
        providerList: state => state.provider.providerList,
        providerTotal: state => state.provider.providerTotal,
        classify: state => state.service.classify,
        clusterOption: state => state.service.clusterOption
      })
    },
    activated() {
      const vm = this
      if (this.clusterOption.length <= 0) {
        vm.initializeData({vm})
      }
      let {zkClusterId} = vm.$route.params
      if (zkClusterId) {
        vm.providerForm = Object.assign({}, {zkClusterId, 'serName': vm.$route.params.name, 'appName': vm.$route.params.appliName})
        vm.search()
      }
    },
    created() {
      const vm = this
      let {zkClusterId} = vm.$route.params
      if (zkClusterId) {
        vm.providerForm = Object.assign({}, {zkClusterId, 'serName': vm.$route.params.name, 'appName': vm.$route.params.appliName})
        vm.search()
      }
    },
    methods: {
      ...mapActions('provider', ['serviceFind', 'providerDisable', 'providerEnable', 'providerMock', 'providerRecoverMock', 'providerWeight', 'providerLoadBal']),
      ...mapActions('service', ['initializeData']),
      // 翻页事件
      handleCurrentChange(val) {
        this.currentPage = val
      },
      // 查询
      search(title) {
        if (!this.providerForm.zkClusterId) {
          this.$message({
            message: '集群名称不能为空！',
            type: 'warning'
          })
          return
        }
        if (title === 'search') {
          this.currentPage = 1
        }
        const vm = this
        vm.loading = true
        vm.serviceFind(vm)
      },
      // 拿到选择的内容
      chooseBox(val) {
        this.tableVal = val
      },
      // 禁用
      stopBtn(props) {
        const vm = this
        if (props) {
          vm.handlePara = {
            'serName': props.row.serName,
            'zkClusterId': vm.providerForm.zkClusterId,
            'ip': props.row.ip,
            'port': props.row.port,
            'disabled': 'true'
          }
        }
        if (!props) {
          let _serName = []
          let _ip = []
          let _port = []
          vm.tableVal.forEach(item => {
            _serName.push(item.serName)
            _ip.push(item.ip)
            _port.push(item.port)
          })
          vm.handlePara = {
            'serName': _serName.toString(),
            'zkClusterId': vm.providerForm.zkClusterId,
            'ip': _ip.toString(),
            'port': _port.toString(),
            'disabled': 'true'
          }
        }
        vm.makeTrue('禁用').then(() => {
          vm.providerDisable(vm)
        })
      },
      // 启用
      startBtn(props) {
        const vm = this
        if (props) {
          vm.handlePara = {
            'serName': props.row.serName,
            'zkClusterId': vm.providerForm.zkClusterId,
            'ip': props.row.ip,
            'port': props.row.port,
            'disabled': 'false'
          }
        }
        if (!props) {
          let _serName = []
          let _ip = []
          let _port = []
          vm.tableVal.forEach(item => {
            _serName.push(item.serName)
            _ip.push(item.ip)
            _port.push(item.port)
          })
          vm.handlePara = {
            'serName': _serName.toString(),
            'zkClusterId': vm.providerForm.zkClusterId,
            'ip': _ip.toString(),
            'port': _port.toString(),
            'disabled': 'false'
          }
        }
        vm.makeTrue('启动').then(() => {
          vm.providerEnable(vm)
        })
      },
      // 权重调节
      weightBtn(props) {
        const vm = this
        vm.popWay = vm.$t('service.provider.weight')
        vm.popForm.weight = '' // 清空
        vm.popBox = true
        if (props) {
          vm.serviceActive = 'single'
          vm.popForm.weight = props.row.weight
          vm.handlePara = {
            'serName': props.row.serName,
            'zkClusterId': vm.providerForm.zkClusterId,
            'ip': props.row.ip,
            'port': props.row.port,
            'weight': vm.popForm.weight
          }
        }
        if (!props) {
          let _serName = []
          let _ip = []
          let _port = []
          vm.tableVal.forEach(item => {
            _serName.push(item.serName)
            _ip.push(item.ip)
            _port.push(item.port)
          })
          vm.handlePara = {
            'serName': _serName.toString(),
            'zkClusterId': vm.providerForm.zkClusterId,
            'ip': _ip.toString(),
            'port': _port.toString(),
            'weight': vm.popForm.weight
          }
          vm.serviceActive = 'more'
        }
      },
      // 负载均衡
      loadChange(props) {
        const vm = this
        vm.popWay = vm.$t('service.provider.loadbBalance')
        vm.popForm.loadbBalance = '' // 清空
        vm.popBox = true
        if (props) {
          vm.serviceActive = 'single'
          vm.popForm.loadbBalance = props.row.loadbalance
          vm.handlePara = {
            'serName': props.row.serName,
            'zkClusterId': vm.providerForm.zkClusterId,
            'loadbalance': props.row.loadbalance
          }
        }
        if (!props) {
          let _serName = []
          vm.tableVal.forEach(item => {
            _serName.push(item.serName)
          })
          vm.handlePara = {
            'serName': _serName.toString(),
            'zkClusterId': vm.providerForm.zkClusterId,
            'loadbalance': props.row.loadbalance
          }
          vm.serviceActive = 'more'
        }
      },
      // 降级
      demotionBtn(props) {
        const vm = this
        vm.popWay = vm.$t('service.provider.demotion')
        vm.popBox = true
        if (props) {
          vm.serviceActive = 'single'
          vm.popForm.demotion = props.row.mockStatus === '0' ? '' : props.row.mockStatus
          vm.handlePara = {
            'serName': props.row.serName,
            'zkClusterId': vm.providerForm.zkClusterId,
            'ip': props.row.ip,
            'mock': props.row.mockStatus
          }
        }
        if (!props) {
          let _serName = []
          let _ip = []
          vm.tableVal.forEach(item => {
            _serName.push(item.serName)
            _ip.push(item.ip)
          })
          vm.handlePara = {
            'serName': _serName.toString(),
            'zkClusterId': vm.providerForm.zkClusterId,
            'ip': _ip.toString(),
            'mock': vm.popForm.demotion
          }
          vm.serviceActive = 'more'
        }
      },
      // 降级恢复
      demotionRecover(props) {
        const vm = this
        if (props) {
          vm.serviceActive = 'single'
          vm.handlePara = {
            'serName': props.row.serName,
            'zkClusterId': vm.providerForm.zkClusterId,
            'ip': props.row.ip
          }
        }
        if (!props) {
          let _serName = []
          let _ip = []
          vm.tableVal.forEach(item => {
            _serName.push(item.serName)
            _ip.push(item.ip)
          })
          vm.handlePara = {
            'serName': _serName.toString(),
            'zkClusterId': vm.providerForm.zkClusterId,
            'ip': _ip.toString()
          }
          vm.serviceActive = 'more'
        }
        vm.makeTrue('降级恢复').then(() => {
          vm.providerRecoverMock(vm)
        })
      },
      // 弹出框确定
      serviceOk() {
        const vm = this
        // 权重
        if (vm.popWay === vm.$t('service.provider.weight')) {
          if (vm.serviceActive === 'single') {
            vm.handlePara.weight = vm.popForm.weight
            vm.providerWeight(vm)
          }
          if (vm.serviceActive === 'more') {
            vm.handlePara.weight = vm.popForm.weight
            vm.providerWeight(vm)
          }
        }
        // 均衡
        if (vm.popWay === vm.$t('service.provider.loadbBalance')) {
          if (vm.serviceActive === 'single') {
            vm.handlePara.loadbalance = vm.popForm.loadbBalance
            vm.providerLoadBal(vm)
          }
          if (vm.serviceActive === 'more') {
            vm.handlePara.loadbalance = vm.popForm.loadbBalance
            vm.providerLoadBal(vm)
          }
        }
        // 降级
        if (vm.popWay === vm.$t('service.provider.demotion')) {
          vm.handlePara.mock = vm.popForm.demotion
          if (vm.serviceActive === 'single') {
            vm.providerMock(vm)
          }
          if (vm.serviceActive === 'more') {
            vm.providerMock(vm)
          }
        }
        vm.popBox = false
      },
      serviceCancel() {
        this.$refs.popForm.resetFields()
        this.popBox = false
      },
      // 确定工具
      makeTrue(words) {
        let _this = this
        return new Promise(function (resolve, reject) {
          _this.$confirm('是否' + words, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            resolve()
          }).catch(() => {
            _this.$message({
              type: 'info',
              message: '已取消该操作'
            })
          })
        })
      }
    }
  }
</script>



